/*-------------------------*/
/* $Globel
---------------------------*/
.wrapper{
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
}

/*-------------------------*/
/* $Elements 
---------------------------*/

/*-------------------------*/
/* $Header
---------------------------*/
.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    transition: all 0.6s;
}
.header_bg{
    background-color: rgba(196, 213, 223, 0.6);
}
.header_title{
    float:left;
    margin-left: 38px;
}
.header_nav{
    right: 40px;
}
.navbar{
    float: right;
    padding: 0;
    right: 0px;
}
.nav_item{
    float:left;
    padding: 0 25px;
    transition: all 0.3s;
    margin-top: 16px;
    margin-bottom: 16px;
    cursor: pointer;
}
.nav_item:hover > a{
    color: #fff;
}
.nav_item  h4:after{
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s;
}
.nav_item  h4:hover::after{
    width: 100%;
}
.resume{
    cursor: pointer;
}
.header_nav .nav_icon{
    right: 20px;
    transition: all 0.3s;
}
@media (max-width:992px){
    .header_nav, .head_nav{
        right: 0px;
    }
    .navbar{
        position: absolute;
        top:0;
        right: 0;
        width: 30%;
        height: 100vh;
        background-color: #fefefe;
        float: none;
        color: #707070;
        align-items: initial;
        transition: all 0.3s;
    }
    .hide_nav_bar{
        transform: translateX(100%);
    }
    .header_nav ul{
        top: 5px;
    }
    .header_nav h4{
        color: #707070;
    }
    .navbar_items{
        margin-top: 40px;
    }
    .nav_item{
        float: none;
        padding: 0px 40px;
        margin-top: 16px;
        margin-bottom: 16px;

    }
    .nav_item a h4:after{
        background-color: #707070;
    }
    .side_nav_box_shadow{
        box-shadow: -3px -3px 6px rgba(0, 0, 0, 0.16);
    }
}
@media (max-width:768px){
    .header_nav .nav_icon{
        right: 20px;
    }
}
@media (max-width:576px){
    .navbar{
        width: 40%;
    }
}
.nav_icon{
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 50;
}
.span{
    display: block;
    width: 30px;
    height: 3px;
    background-color: #fff;
    margin-bottom: 8px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
    box-shadow: 0.5px 0.5px 1px #c7c7c7;
}
@media (max-width:992px){
    .span{
        opacity: 1;
        visibility: visible;
    }
}
@media (max-width:768px){
    .span{
        width: 30px;
        height: 2px;
    }
}
@media (max-width:425px){
    .span{
        width: 20px;
        height: 1px;
        margin-bottom: 6px;
    }
}
.span_top{
    background-color: #707070;
    position: absolute;
    top:50%;
    transform: translateY(-50%) rotate(225deg);
}
.span_md{
    opacity: 0;
}
.span_bottom{
    background-color: #707070;
    position: absolute;
    top:50%;
    transform: translateY(-50%) rotate(-225deg);
}
.scrollLine{
    position: absolute;
    top: 0;
    left: 0;
    height: 4px;
    background-color: #99AABA;
    opacity: 0.6;
    z-index: -10;
    transition: all 0.1s;
}
@media(min-width:992px){
    .scrollLine{
        height: 69px;
    }
}
/*-------------------------*/
/* $Head
---------------------------*/
.head .imgbox{
    position: relative;
    top: 0;
    left: 0;
	width: 100%;
    overflow: hidden;
}
.head .imgbox .image_inner{
	width: 100%;
	padding-top: 40%;
	position: relative;
	height: 0;
}
@media(max-width:576px){
    .head .imgbox .image_inner{
        padding-top: 60%;
    }

}
.head .imgbox .image_inner .image{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
    background-image: url("../images/head_bg.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}
@media (max-width:992px){
    .head_nav .navbar{
        opacity: 0;
        visibility: hidden;
    }
    .nav_icon span{
        opacity: 1;
        visibility: visible;
    }
}
.head a:hover{
    color:#fefefe;
}
.head_title_box{
    position: absolute;
    top: 35%;
    left: 0;
    width: 100%;
    height: 100%;
}
.head_title{
    width: 100%;
    display: flex;
	justify-content: center;
	align-items: center;
}
@media (max-width:768px){
    .head_title h1{
        font-size:20px;
    }
}
@media (max-width:576px){
    .head_title h1{
        font-size:15px;
    }
}
.head_title .head_img{
    width: 100px;
    height: auto;
    padding-bottom: 30px;
}
@media (max-width:992px){
    .head_title .head_img{
        width: 70px;
        padding-bottom: 20px;
    }
}
@media (max-width:768px){
    .head_title .head_img{
        width: 40px;
        padding-bottom: 5px;
    }
}
@media (max-width:576px){
    .head_title .head_img{
        width: 30px;
        padding-bottom: 5px;
    }
}
.head_title .head_title_text{
    text-shadow: 1px 3px 6px rgba(0, 0, 0, 0.16) ;
}
.head_scroll{
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
}
.head_scroll img{
    opacity: 1;
    transform: translateY(0%);
    animation: scroll_arrow 1.5s 0s infinite ease-out;
}
@keyframes scroll_arrow{
    0%{
        opacity: 0;
        transform: translateY(0%);
    }
    70%{
        opacity: 1;
        transform: translateY(30%);
    }
    100%{
        opacity: 0;
        transform: translateY(30%);
    }
}
@media (max-width:992px){
    .head_scroll{
        bottom: 20px;
    }
    .head_scroll .head_scroll_text{
        font-size: 16px;
        line-height: 20px;
    }
    .head_scroll img{
        width: 20px;
    }
}
@media (max-width:768px){
    .head_scroll{
        bottom: 5px;
    }
    .head_scroll .head_scroll_text{
        font-size: 12px;
        line-height: 14px;
    }
    .head_scroll img{
        width: 15px;
    }
}
@media (max-width:425px){
    .head_scroll{
        bottom: 0px;
    }
    .head_scroll .head_scroll_text{
        font-size: 8px;
        line-height: 10px;
    }
    .head_scroll img{
        width: 10px;
    }
}
