}
@media(max-width:768px){
    .highlight_title{
        margin-bottom: 20px;
    }
}
.highlight_item{
    margin-bottom: 36px;
}
.highlight_box{
    overflow: hidden;
    width: 100%;
    border-radius: 12px;
    background-color: #fefefe;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.16);
    transition: all 0.3s;
}
.highlight_box:hover{
    cursor:pointer;
    transform: translate( -2px, -2px);
    box-shadow: 8px 8px 15px rgba(0, 0, 0, 0.16);
}
.highlight_box:active{
    transform: translate( 2px, 2px);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.16);
}
.highlight_box .image::before{
    content: " ";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    /* background-color: rgba(196, 213, 223, 0.5); */
    background-color: rgba(256, 256, 256, 0.8);
    opacity: 0;
    transition: all 0.3s;
}
.highlight_box .image:after{
    content: "Learn More";
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    line-height: 46px;
    color: #E2B49C;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.16);
    opacity: 0;
    transition: all 0.5s;
}
.highlight_box:hover .image:before{
    opacity: 1;
}
/* no learn more */
.highlight_box:hover .image:after{
    opacity: 1;
}
