.article{
    padding-top: 10px;
}
.detail{
    padding-top: 5px;
}
.detail p{
    color: rgb(187, 187, 187);
}

/* image */
.imgBox{
    width:90%;
    padding: 0 0;
    margin-bottom: 10px;
}
.imgBox .img_inner{
    width: 100%;
    padding-top: 50%;
    position: relative;
    height: 0;
}
.imgBox .img_inner .img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 1px 1px 3px #c7c7c7;
}
.ai .image-area{
    display: flex;
}
.ai .imgBox.taichi{
    width:50%;
    margin-right: 10px;
}
.ai .imgBox .img_inner{
    width: 100%;
    padding-top: 70%;
    position: relative;
    height: 0;
}
.ai .imgBox.taichi .img_inner{
    width: 100%;
    padding-top: 95%;
    position: relative;
    height: 0;
}
.officer .imgBox .img_inner{
    width: 100%;
    padding-top: 60%;
}
@media(min-width:992px){
    .article{
        overflow-y: scroll;
        padding-right: 20px;
    }
}
@media(max-width:991px){
    .article{
        padding-left: 20px;
        padding-right: 20px;
    }
}
/* title */
.article .title {
    letter-spacing: 1px;
}
.article .title a{
    color: #707070;
    display: flex;
    align-items: center;
    transition: all 0.3s;
}
.article .title a:hover{
    color: #c7c7c7;
}
.article .title span{
    margin-left: 10px;
    padding: 4px 10px;
    font-size: 12px;
    line-height: 12px;
    border-radius: 8px;
    border: 1px solid #c7c7c7;
    transition: all 0.3s;
}
.article .title a:hover span{
    background-color: #c7c7c7;
    color: #fefefe;
}
@media(max-width:576px){
    .article .title span{
        padding: 3px 10px;
        padding-top: 5px;
        font-size: 10px;
        line-height: 10px;
        border-radius: 6px;
    }
}
/* content */
.article .content{
    padding-top: 20px;
}
@media(min-width:992px){
    .article .content{
        padding-left: 20px;
        padding-right: 60px;
    }
}
@media(min-width:768px){
    .article .content h3{
        font-weight: 500;
        font-size: 24px;
        line-height: 24px;
    }
}
.article .content h4{
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    padding: 10px 0;
}
.article .content h5{
    font-weight: 300;
}
.article .content a{
    color: #707070;
}
.article .content a:hover{
    color: #c7c7c7;
}

@media(max-width:768px){
    .article .content h4{
        font-size: 18px;
        line-height: 30px;
    }
}
@media(max-width:425px){
    .article .content h4{
        font-size: 16px;
        line-height: 28px;
    }
}
.article .content span{
    color:#99AABA;
    font-weight: 500;
}
/* info */
.brief_info{
    padding-top: 30px;
}
.photoBox{
    width: 100%;
}
.photoBox .photo_inner{
    width: 100%;
    padding-top: 100%;
    position: relative;
    height: 0;
}
.photoBox .photo_inner .photo{
    border-radius: 12px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    box-shadow: 0 3px 6px #c7c7c7;
}
.brief_info_items{
    margin-top: 30px;
}
.hello{
    margin-top: 30px;
    margin-bottom: 30px;
    border-radius: 12px;
    box-shadow: 0 3px 6px #c7c7c7;
    padding: 20px 20px;
}
/* react carousel */
.myCarousel .carousel .carousel-status{
    text-shadow: 0 0 0 #fefefe;
    color: #707070;
}
.myCarousel .carousel .control-dots .dot{
    display: none;
}
.myCarouselObj{
    max-width: 80%;
    max-height: 450px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
@media(max-width:992px){
    .myCarouselObj{
        max-height: 390px;
    }
}
@media(max-width:768px){
    .myCarouselObj{
        max-height: 290px;
    }
}
@media(max-width:576px){
    .myCarouselObj{
        max-height: 260px;
    }
}
@media(max-width:425px){
    .myCarouselObj{
        max-height: 190px;
    }
}
@media(max-width:320px){
    .myCarouselObj{
        max-height: 170px;
    }
}
.club2 .myCarouselObj{
    width:40%;
}
.myCarouselObj img{
    box-shadow: 1px 1px 4px #c7c7c7;
    margin-bottom: 10px;
}
.myCarousel .carousel .thumb.selected{
    border: 1px solid #c7c7c7;
}
.myCarousel .carousel .thumb:hover{
    border: 3px solid #c7c7c7;
}
.projectManagement .myCarouselObj{
    max-width: 20%
}
/* video */
.video{
    width: 560px;
    height: 315px;
}
@media(max-width:768px){
    .video{
        width: 420px;
        height: 237px
    }
}
@media(max-width:576px){
    .video{
        width: 280px;
        height: 158px
    }
}

