/*-------------------------*/
/* $Color
---------------------------*/
.bg-color-primary{background-color: #C4D5DF;}
.bg-color-secondary{background-color: #E2B49C;}
.bg-color-dark-blue{background-color: #99AABA}
.bg-color-yellow{background-color: #E8C77A;}
.bg-color-grey{background-color: #707070;}
.bg-color-white{background-color: #fefefe;}

/* text color */
.color-primary{color: #C4D5DF;}
.color-secondary{color: #E2B49C;}
.color-dark-blue{color: #99AABA}
.color-yellow{color: #E8C77A;}
.color-grey{color: #707070;}
.color-white{color: #fefefe;}

/*-------------------------*/
/* $Typography 
---------------------------*/
body,html{
    font-family: 'Josefin Sans', 'Helvetica', 'roboto', arial, sans-serif;
  }
  /* heading */
  h1,h2,h3,h4,h5,h6{
    margin: 0;
    font-weight: 500;
    -webkit-user-drag: none;
  }
  h1::selection, h2::selection, h2 > div::selection, h3::selection, h4::selection, h5::selection, h6::selection, br::selection, a::selection, span::selection, li::selection, p::selection{
    color: rgba(0, 0, 0, 0.4);
    background-color: #C4D5DF;
  }
  h1{
    font-size: 42px;
    font-weight: 700;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 50px;
    padding-top: 8px;
  }
  h2{
    font-size: 40px;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    font-weight: 700;
    /* margin-top: 24px;
    margin-bottom: 24px; */
  }
  h3{
    font-size: 24px;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 48px;
    /* margin-top: 24px;
    margin-bottom: 24px; */
  }
  h4{
    font-size: 20px;
    font-weight: 500;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 32px;
    padding-top: 5px;
    letter-spacing: 1px;
  }
  h5{
    font-size: 16px;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 20px;
    letter-spacing: 1px;
    /* margin-top: 24px;
    margin-bottom: 16px; */
  }
  h6{
    font-size: 12px;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 14px;
  }
  p{
    font-size: 16px;
    font-weight: 500;
    font-family: 'Josefin Sans', 'Noto Sans TC', 'roboto', arial, sans-serif;
    line-height: 24px;
    letter-spacing: 0;
    margin-bottom: 0;
  }
  @media (max-width: 992px){
    h1{
      font-size: 40px;
    }
  }
  @media (max-width: 768px){
    h1{
      font-size: 35px;
    }
    h2{
      font-size: 32px;
      line-height: 38px;
    }
    h3{
      font-size: 20px;
     line-height: 32px;
    }
  }
  @media (max-width: 425px){
    h1{
      font-size: 30px;
    }
    h2{
      font-size: 24px;
      line-height: 24px;
    }
    h3{
      font-size: 18px;
      line-height: 20px;
    }
    h4{
      font-size: 16px;
     line-height: 24px;
    }
    h5{
      font-size: 14px;
      line-height: 20px;
    }
  }
  .subtitle{
    font-size: 24px;
    font-family: 'Josefin Sans', 'Helvetica', 'roboto', arial, sans-serif;
    line-height: 32px;
    margin-top: 16px;
    margin-bottom: 16px;
    padding-top: 2px;
  }
  @media (max-width: 768px){
    .subtitle{
      font-size: 20px;
    }
  }
  /* link */
  a{
    color: #fefefe;
    text-decoration: none;
  }
  a:hover,
  a:active,
  a:fucus{
    color: #fefefe;
    text-decoration: none;
  }
  /* list */
  ul{
    list-style: none;
    margin: 0;
    padding: 0;
  }

