/*-------------------------*/
/* $Utility 
---------------------------*/
/* old version css */
.u-clearfix:before,
.u-clearfix:after,
.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after {
  content: " ";
  display: table;
}
.u-clearfix:after,
.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}
.u-align-right{
  text-align: right;
}
@media(max-width:992px){
  .u-align-right{
    text-align: left;
  }
}